<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>Montagechecklist</h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Gebouw</h4>
        </div>
        <div class="input-container">
          <label class="label">Toegang</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.entrance"
              native-value="Enkele deur"
              type="is-primary"
            >
              <span>Enkele deur</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.entrance"
              native-value="Dubbele deur/roldeur"
              type="is-primary"
            >
              <span>Dubbele deur/roldeur</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Lift aanwezig?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.elevator"
              native-value="Ja"
              type="is-success"
            >
              <span>Ja</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.elevator"
              native-value="Nee"
              type="is-danger"
            >
              <span>Nee</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Passen de panelen in de lift?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.elevatorFit"
              native-value="Ja"
              type="is-success"
            >
              <span>Ja</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.elevatorFit"
              native-value="Nee"
              type="is-danger"
            >
              <span>Nee</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Foto's gemaakt?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.pictures"
              native-value="Ja"
              type="is-success"
            >
              <span>Ja</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.pictures"
              native-value="Nee"
              type="is-danger"
            >
              <span>Nee</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Plattegrond schets aanwezig?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.groundPlan"
              native-value="Ja"
              type="is-success"
            >
              <span>Ja</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.groundPlan"
              native-value="Nee"
              type="is-danger"
            >
              <span>Nee</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Kunnen goederen eerder worden geleverd?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.earlyProductDelivery"
              native-value="Ja, opslag aanwezig"
              type="is-success"
            >
              <span>Ja, opslag aanwezig</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.earlyProductDelivery"
              native-value="Nee, levering op montagedag"
              type="is-danger"
            >
              <span>Nee, levering op montagedag</span>
            </b-radio-button>
          </b-field>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Plafond</h4>
        </div>
        <div class="input-container">
          <label class="label">Plafondhoogte (meter)</label>
          <base-input
            type="number"
            class="s"
            placeholder="0"
            v-model="assemblyChecklist.ceilingHeight"
          />
        </div>
        <div class="input-container">
          <label class="label">Soort plafond</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.ceilingType"
              native-value="Beton"
              type="is-primary"
            >
              <span>Beton</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.ceilingType"
              native-value="Gips"
              type="is-primary"
            >
              <span>Gips</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.ceilingType"
              native-value="Stro/stuc"
              type="is-primary"
            >
              <span>Stro/stuc</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.ceilingType"
              native-value="Staal"
              type="is-primary"
            >
              <span>Staal</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.ceilingType"
              native-value="Hout"
              type="is-primary"
            >
              <span>Hout</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Lampen</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.lights"
              native-value="Niet aanwezig"
              type="is-primary"
            >
              <span>Niet aanwezig</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.lights"
              native-value="Doorvoeren"
              type="is-primary"
            >
              <span>Doorvoeren</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.lights"
              native-value="Omheen werken"
              type="is-primary"
            >
              <span>Doorvoeren</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Steiger nodig?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.scaffolding"
              native-value="Ja"
              type="is-success"
            >
              <span>Ja</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.scaffolding"
              native-value="Nee"
              type="is-danger"
            >
              <span>Nee</span>
            </b-radio-button>
          </b-field>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Wanden</h4>
        </div>
        <div class="input-container">
          <label class="label">Soort wand</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.wallType"
              native-value="Beton"
              type="is-primary"
            >
              <span>Beton</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.wallType"
              native-value="Gips"
              type="is-primary"
            >
              <span>Gips</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.wallType"
              native-value="Hout"
              type="is-primary"
            >
              <span>Hout</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.wallType"
              native-value="Staal"
              type="is-primary"
            >
              <span>Staal</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Soort montage</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.wallAssembly"
              native-value="Vlak op de wand"
              type="is-primary"
            >
              <span>Vlak op de wand</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.wallAssembly"
              native-value="Met airgap"
              type="is-primary"
            >
              <span>Met airgap</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Stopcontacten doorvoeren?</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.outlets"
              native-value="Ja"
              type="is-success"
            >
              <span>Ja</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.outlets"
              native-value="Nee"
              type="is-danger"
            >
              <span>Nee</span>
            </b-radio-button>
          </b-field>
        </div>
        <div class="input-container">
          <label class="label">Plint</label>
          <b-field>
            <b-radio-button
              v-model="assemblyChecklist.baseboard"
              native-value="Op plint"
              type="is-primary"
            >
              <span>Op plint</span>
            </b-radio-button>
            <b-radio-button
              v-model="assemblyChecklist.baseboard"
              native-value="Boven plint"
              type="is-primary"
            >
              <span>Boven plint</span>
            </b-radio-button>
          </b-field>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="saveAssemblyChecklist"
        :loading="savingAssemblyChecklist"
        :disabled="savingAssemblyChecklist"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Opslaan
      </base-button>
    </div>
  </div>
</template>

<script>
import Firebase from "firebase";

export default {
  data: function() {
    return {
      savingAssemblyChecklist: false,
      assemblyChecklist: {
        entrance: "",
        elevator: "",
        elevatorFit: "",
        pictures: "",
        groundPlan: "",
        earlyProductDelivery: "",
        ceilingHeight: "",
        ceilingType: "",
        lights: "",
        scaffolding: "",
        wallType: "",
        wallAssembly: "",
        outlets: "",
        baseboard: ""
      }
    };
  },
  computed: {
    currentProject: function() {
      return this.$store.getters.getCurrentClientPage.project;
    }
  },
  methods: {
    saveAssemblyChecklist: function() {
      this.savingAssemblyChecklist = true;
      Firebase.firestore()
        .collection("projects")
        .doc(this.currentProject.id)
        .set(
          {
            assemblyChecklist: this.assemblyChecklist
          },
          { merge: true }
        )
        .then(() => {
          this.$store.dispatch("fetchProjects").then(() => {
            this.savingAssemblyChecklist = false;
            this.closeModal();
          });
        })
        .catch(error => {
          console.log("Error saving assemblyChecklist: ", error);
        });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "assemblyChecklist",
        modalType: "view",
        shown: false
      });
    }
  },
  mounted: function() {
    this.assemblyChecklist = Object.assign(
      {},
      this.currentProject.data().assemblyChecklist
    );
  }
};
</script>


